// import React from "react";
import { useState } from "react";
import { Container, Row, Col, Card, Button, ListGroup } from "react-bootstrap";
import profileImage from "../images/profile.jpg"; // <-- Replace with your image path
import microsoft_logo from "../images/microsofticon.png"; // <-- Replace with your image path
import sql_pro from "../images/sql_server.jpg"; // <-- Replace with your image path
import powerbi from "../images/powerbi.jpg"; // <-- Replace with your image path
import python from "../images/python.jpg"; // <-- Replace with your image path

import { FaLinkedin, FaGithub, FaYoutube, FaFacebook, FaTiktok, FaUserTie } from "react-icons/fa"; // social icons
import { FaUniversity, FaBrain, FaChalkboardTeacher } from "react-icons/fa";
import { Link, useLocation } from 'react-router-dom';
import './DriveVideoEmbed.css';
function HomeScreen() {
  const [activated, setActivated] = useState(false);
  return (
    <div
      style={{
        backgroundColor: "#EBE7E4",
        fontFamily: "'Segoe UI', system-ui, sans-serif",
        color: "#CD7F32",
      }}
    >
    <Container className="py-5">
      {/* ===== Hero Section ===== */}
      <Row style={{
        border: "none",
        borderRadius: "12px",
        backgroundColor: "white",

      }} className="p-4 mb-4 shadow-sm align-items-center">
       {/* TEXT SECTION */}
      <Col
        md={8}
        sm={12}
        className="text-center text-md-start mb-4 mb-md-0 hero-text-container"
      >
        <style>
          {`
            /* ---------- Hero Section Responsive Design ---------- */
            .hero-text-container {
              background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
              padding: 2rem 1.5rem;
              border-radius: 1rem;
              box-shadow: 0 4px 15px rgba(0,0,0,0.08);
              transition: all 0.3s ease;
            }

            @media (max-width: 767.98px) {
              .hero-text-container {
                padding: 1.5rem 1rem;
              }
            }

            /* ---------- Headings ---------- */
            .hero-text-container h1 {
              color: #212529;
              line-height: 1.3;
              font-size: clamp(1.8rem, 4vw, 3rem); /* Responsive font scaling */
            }

            .hero-highlight {
              color: #DAA520;
              font-weight: 700;
            }

            /* ---------- List Items ---------- */
            .list-item {
              transition: transform 0.2s ease, color 0.2s ease;
            }

            .list-item:hover {
              transform: translateX(6px);
              color: #0d6efd;
            }

            .list-item svg {
              flex-shrink: 0;
            }

            /* ---------- Social Icons ---------- */
            .text-linkedin { color: #0A66C2; }
            .text-youtube { color: #FF0000; }
            .text-facebook { color: #1877F2; }
            .text-tiktok { color: #000000; }
            .text-dark { color: #212529; }

            .social-link {
              text-decoration: none;
              transition: all 0.25s ease;
              display: inline-flex;
              align-items: center;
              justify-content: center;
              width: 45px;
              height: 45px;
              border-radius: 50%;
              background-color: #FDF9EB;
            }

            @media (max-width: 575.98px) {
              .social-link {
                width: 38px;
                height: 38px;
                font-size: 1.2rem;
              }
            }

            .social-link:hover {
              transform: translateY(-4px);
              opacity: 0.9;
            }

            /* Brand color hover effect */
            .social-link:hover.text-linkedin { background-color: #0A66C2; color: #fff; }
            .social-link:hover.text-youtube { background-color: #FF0000; color: #fff; }
            .social-link:hover.text-facebook { background-color: #1877F2; color: #fff; }
            .social-link:hover.text-tiktok { background-color: #000000; color: #fff; }
            .social-link:hover.text-dark { background-color: #333333; color: #fff; }

            /* ---------- Mobile Adjustments ---------- */
            @media (max-width: 767.98px) {
              .hero-text-container h1 {
                text-align: center;
              }

              .list-item {
                justify-content: center;
                text-align: center;
              }

              .d-flex.justify-content-md-start {
                justify-content: center !important;
              }
            }
          `}
        </style>

        {/* Heading */}
        <h1 className="fw-bold display-5 mb-3">
          <span className="hero-highlight">Hi, I'm {"{DUY VAN}"}</span>
          <br />
          <span className="hero-highlight">M.Sc. in Data Science</span>
        </h1>

        {/* List Items */}
        <ListGroup variant="flush" className="fs-5 mt-3 text-start">
          <ListGroup.Item className="list-item fs-6 bg-transparent border-0 d-flex align-items-center text-nowrap">
            <FaUniversity className="me-3 text-info fs-4 flex-shrink-0" />
            M.Sc. in Data Science at LJMU (ENG)
          </ListGroup.Item>

          <ListGroup.Item className="list-item fs-6 bg-transparent border-0 d-flex align-items-center text-nowrap">
            <FaBrain className="me-3 text-warning fs-4 flex-shrink-0" />
            10 Years of Data Analytics, AI
          </ListGroup.Item>

          <ListGroup.Item className="list-item fs-6 bg-transparent border-0 d-flex align-items-center text-nowrap">
            <FaUserTie className="me-3 text-success fs-5 flex-shrink-0" />
            7 Years in Data Manager / Head roles
          </ListGroup.Item>

          <ListGroup.Item className="list-item fs-6 bg-transparent border-0 d-flex align-items-center text-nowrap">
            <FaChalkboardTeacher className="me-3 text-primary fs-4 flex-shrink-0" />
            3 Years Teaching SQL, Python, Power BI
          </ListGroup.Item>
        </ListGroup>

        {/* Social Media Links */}
        <div className="d-flex justify-content-center justify-content-md-start mt-4 mb-4 gap-3 flex-wrap">
          {[
            { href: "https://www.linkedin.com/in/duyvanda/", icon: <FaLinkedin />, color: "text-linkedin" },
            { href: "https://github.com/duyvanda", icon: <FaGithub />, color: "text-dark" },
            { href: "https://www.youtube.com/@duyvandata", icon: <FaYoutube />, color: "text-youtube" },
            { href: "https://www.tiktok.com/@duyvanda", icon: <FaTiktok />, color: "text-tiktok" },
            { href: "https://www.facebook.com/duyvandata/", icon: <FaFacebook />, color: "text-facebook" },
          ].map(({ href, icon, color }, idx) => (
            <a
              key={idx}
              href={href}
              target="_blank"
              rel="noopener noreferrer"
              className={`fs-4 ${color} social-link`}
              aria-label={href}
            >
              {icon}
            </a>
          ))}
        </div>
      </Col>
    
      {/* Image Side */}
      <Col md={4} sm={12} className="text-center mt-3 mb-4 mb-md-0">
        <style>
          {`
            /* --- Pure Green Spinning Glow Profile --- */
            .profile-image-wrapper {
              position: relative;
              width: 100%;
              max-width: 320px;
              aspect-ratio: 1 / 1;
              margin: 0 auto;
            }

            .profile-image-container {
              position: relative;
              overflow: hidden;
              border-radius: 50%;
              width: 100%;
              height: 100%;
              box-shadow: 0 0 30px rgba(0, 255, 100, 0.3);
              transition: transform 0.4s ease, box-shadow 0.4s ease;
            }

            .profile-image-container:hover {
              transform: scale(1.05);
              box-shadow: 0 0 50px rgba(0, 255, 100, 0.6);
            }

            .profile-image-container img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              border-radius: 50%;
              z-index: 2;
              position: relative;
              transition: transform 0.6s ease;
            }

            .profile-image-container:hover img {
              transform: scale(1.08);
            }

            /* --- Spinning green light ring --- */
            .spinning-ring {
              position: absolute;
              top: -8px;
              left: -8px;
              width: calc(100% + 16px);
              height: calc(100% + 16px);
              border-radius: 50%;
              background: conic-gradient(
                from 0deg,
                rgba(0, 255, 150, 0.0) 0deg,
                rgba(0, 255, 200, 0.5) 45deg,
                rgba(0, 255, 80, 0.9) 90deg,
                rgba(0, 255, 150, 0.0) 135deg
              );
              animation: spinLight 3s linear infinite;
              z-index: 1;
              filter: blur(4px);
              opacity: 1;
            }

            @keyframes spinLight {
              from { transform: rotate(0deg); }
              to { transform: rotate(360deg); }
            }

            /* --- Soft green pulse --- */
            .profile-image-wrapper::after {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              border-radius: 50%;
              box-shadow: 0 0 60px rgba(0, 255, 100, 0.25);
              animation: pulseOuter 5s ease-in-out infinite;
            }

            @keyframes pulseOuter {
              0%, 100% { opacity: 0.3; transform: scale(1); }
              50% { opacity: 0.7; transform: scale(1.06); }
            }

            /* --- Responsive --- */
            @media (max-width: 768px) {
              .profile-image-wrapper {
                max-width: 240px;
              }
            }
          `}
        </style>

        <div className="profile-image-wrapper">
          <div className="spinning-ring"></div>
          <div className="profile-image-container">
            <img src={profileImage} alt="Profile" className="img-fluid" />
          </div>
        </div>
      </Col>
      </Row>
    
    {/* VIDEO SECTION */}
      <Row className="mt-5 g-4" style={{ 
        height: "700px",
        border: "none",
        borderRadius: "12px",
        backgroundColor: "white",
      }}> 
        <Col md={4}>
          <Card className="shadow-sm text-center border-1">
            <Link to="/sql-pro" style={{ textDecoration: "none" }}>
            {/* Option A: Image at the Top of the Card */} 
            <Card.Img
              variant="top"
              src={sql_pro}
              alt="SQL Certificate"
              style={{
                height: "auto", // Set a fixed height
                width: "100%",   // Make it take the full width of the card
              }}
            />
            </Link>

            <Card.Body className="bg-light">
              {/* <div className="fs-1 mb-3"></div> */}
              {/* <Card.Title>MICROSOFT SQL PROFESSIONAL CERTIFICATE</Card.Title> */}
              <Card.Text >
                <p>Master data extraction, transformation, and analysis using SQL.</p>
                <p>From basic SELECTs to complex window functions.</p>
              </Card.Text>
            </Card.Body>

            {/* Option B: Image at the Bottom of the Card */}
            {/* <Card.Img variant="bottom" src="path/to/your/image.jpg" alt="SQL Certificate" /> */}
          </Card>
        </Col>

        <Col md={4}>
          <Card className="shadow-sm text-center border-1">
            <Link to="/powerbi_pro" style={{ textDecoration: "none" }}>
            <Card.Img
              variant="top"
              src={powerbi}
              alt="SQL Certificate"
              style={{
                height: "auto", // Set a fixed height
                width: "100%",   // Make it take the full width of the card
              }}
            />
            </Link>

            <Card.Body className="bg-light">
              {/* <div className="fs-1 mb-3"></div> */}
              {/* <Card.Title>Python</Card.Title> */}
              <Card.Text>
                <p>Learn Python for data analysis, automation, and integrating with SQL databases.</p>
                <p>Learn Python for data analysis, automation.</p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>

        <Col md={4}>
          <Card className="shadow-sm text-center border-1">
            <Link to="/python_pro" style={{ textDecoration: "none" }}>
            <Card.Img
              variant="top"
              src={python}
              alt="SQL Certificate"
              style={{
                height: "auto",
                width: "100%",
              }}
            />
            </Link>

            <Card.Body className="bg-light">
              {/* <div className="fs-1 mb-3"></div> */}
              {/* <Card.Title>Power BI</Card.Title> */}
              <Card.Text>
                <p>Learn Python for data analysis, automation, and integrating with SQL databases.</p>
                <p>Learn Python for data analysis, automation.</p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      </Container>
     </div>
  )
 
}

export default HomeScreen;
