import { Card, Container, Row, Col } from 'react-bootstrap';
import {
  FaUserMd,
  FaPlusCircle,
  FaGift,
  FaCogs
} from 'react-icons/fa';
import { useHistory } from 'react-router-dom';

const NavigationCards = () => {
  const history = useHistory(); //  React Router v5 dng useHistory

  // Danh sch cc mc iu hng c thm icon
  const items = [
    {
      stt: 1,
      title: 'Danh sch HCP',
      description: 'y l danh sch HCP.',
      path: '/danh-sach-hcp',
      icon: <FaUserMd size={28} color="#007bff" />
    },
    {
      stt: 2,
      title: 'To mi HCP/PCL',
      description: 'Phn tch d liu theo tng khu vc.',
      path: '/phan-tich-khu-vuc',
      icon: <FaPlusCircle size={28} color="#28a745" />
    },
    {
      stt: 3,
      title: 'Booking chi ph qu tng HCP',
      description: 'Xem bo co doanh thu theo qu.',
      path: '/bao-cao-doanh-thu',
      icon: <FaGift size={28} color="#ffc107" />
    },
    {
      stt: 4,
      title: 'Booking chi ph Gimmick HCP',
      description: 'Phn tch d liu theo tng khu vc.',
      path: '/phan-tich-khu-vuc',
      icon: <FaCogs size={28} color="#6c757d" />
    }
  ];

  const sortedList = [...items].sort((a, b) => a.stt - b.stt);

  return (
    <Container fluid className="p-4" style={{ backgroundColor: '#f8f9fa', minHeight: '100vh' }}>
      <h5 className="mb-4 fw-bold">iu hng</h5>
      <Row xs={1} md={2} lg={2} className="g-4">
        {sortedList.map((item, index) => (
          <Col key={index}>
            <Card
              className="shadow-sm"
              onClick={() => history.push(item.path)} //  ng c php React Router v5
              style={{ cursor: 'pointer' }}
            >
              <Card.Body>
                <div className="mb-2">{item.icon}</div>
                <Card.Title>{item.title}</Card.Title>
                <Card.Text>{item.description}</Card.Text>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default NavigationCards;